import Vue from "vue";
export const doError = function (code) {
    code = parseInt(code);
    switch (code) {
        case 8:
            history.back();
            console.log('');
            break;
        case 9:
            alert("!")
            console.log('admin');
            break;
        case 10:
            console.log('token,');
            //
            Vue.$localStorage.$reset();
            //
            $(document).trigger("ChangeLoginStatus",false);
            location.replace('/#');
            return 10;
            break;
        default:
            return code;
            break;
    }
};


