import Vue from "vue";
import Vuex from "vuex";
import {
    SET_SHOWMYWORD_STATUS,
    SET_SOCIALIMG,
    SET_LOGIN_STATUS,
    SET_SHOW_BIGADMIN_STATUS,
    SET_PLAYING_STATUS,
    SET_MUSIC_DURATION,
    SET_MUSIC_RIGHTNOW,
    SET_CURRENT_MUSIC,
    SET_LOADING_STATUS,
    SET_CAN_AUTOPLAY
} from "./mutation-types";
//vuex
//  vue  vuex
Vue.use(Vuex);

//  store
//  store 

export default new Vuex.Store({
    strict: process.env.NODE_ENV !== 'production',
    //state
    state: {
        isShowMyWords: false,//
        isLogin: false,//
        socialImgUrl: '',//
        isShowBigAdmin: false,//->

        //
        isPlaying: false,//
        isLoading: false,//
        handle: '',//
        currentPercent: 0,//
        volume: 1,//
        duration: 0,//
        rightNow: 0,//
        rightPercent: 0,//
        currentMusicInfo: {},//
        canAutoPlay:false,//
    },
    //
    mutations: {
        //toggle
        [SET_SHOWMYWORD_STATUS] (state) {
            state.isShowMyWords = !state.isShowMyWords;
        },
        //img
        [SET_SOCIALIMG] (state, url) {
            state.socialImgUrl = url;
        },
        //img
        [SET_LOGIN_STATUS] (state, status) {
            state.isLogin = !!status;
        },
        //img
        [SET_SHOW_BIGADMIN_STATUS] (state, status) {
            state.isShowBigAdmin = !!status;
        },

        // music
        // ,
        [SET_PLAYING_STATUS] (state, status) {
            state.isPlaying = !!status;
            if (!state.handle) {
                console.log('state.handle=null')
                return;
            }
            if (status) {
                state.handle.play()
            } else {
                state.handle.pause()
            }
        },
        [SET_MUSIC_DURATION] (state, duration) {
            state.duration = duration;
        },
        [SET_MUSIC_RIGHTNOW] (state, rightNow) {
            state.rightNow = rightNow;
            state.rightPercent = state.rightNow * 100 / state.duration
        },
        [SET_CURRENT_MUSIC] (state, currentMusicInfo) {
            state.handle = new Audio(currentMusicInfo.musicUrl);
            state.handle.preload="none";
            // console.log('isPlaying:'+state.isPlaying)
            // if(state.isPlaying){
            //     console.log('willPlaying')
            //     state.handle.load();
            // }
            // state.isLoading = true;
            state.currentMusicInfo = currentMusicInfo;
        },
        [SET_LOADING_STATUS] (state, status) {
            state.isLoading = status;
        },
        [SET_CAN_AUTOPLAY] (state, status) {
            state.canAutoPlay = status;
        },
    },
})


